<template>
  <div class="player">
    <pre>
      {{rtspServerUrl}}
    </pre>
    <span>Source:</span>
    <input id="source-edit" value="rtsp://camproxy.ru:8554/bars"></input>
    <button

      @click="startStream">Watch</button>
    </br>
    <video
      width="100%"
      id="rtsp_player"
      class="has-text-centered"
      ref="rtspPlayer"
      autoplay />
  </div>
</template>

<script>
import {LogLevel} from 'bp_logger';
import $ from 'jquery'
// import * as plugins from '../plugins'
// const Janus = new janus()
// window.Janus = require('../plugins/janus.js')
import adapter from '../plugins/adapter';
import Janus from '../plugins/janus'

export default {
  props: {
    rtspSrc:{
      type: String,
      default: "rtsp://camproxy.ru:8554/bars"
    }
  },
  name: 'appPlayer',
  components: {

  },
  data: () => ({
    isLoading: false,
    isPlaying: false,
    janusServerUrl: 'camproxy.ru',
    player: null,
    paused: false,
    streaming: null,
    janus: null,
    ports: {
      http: {
        socket: {
          url: 'wss://%s:8989'
        },
        http: {
          url: 'http://%s:8088/janus'
        },
      },
      https: {
        socket: {
          url: 'wss://%s:8989'
        },
        https: {
          url: 'https://%s:8089/janus'
        },
      }
    }
  }),
  watch: {

  },
  created(){
    window.player = this.player
  },
  computed: {
    client(){
      return this.player.client
    },
    isStoped(){
      return  this.playerEl.client.paused
    },
    playerEl(){
      return this.$refs.rtspPlayer
    },
    rtspHost(){
      return window.location.protocol.replace(':', '')
    },
    isSupportWebsocket(){
      return window.WebSocket ? true : false
    },
    rtspServerUrl(){
      let url = this.ports[this.rtspHost][this.isSupportWebsocket ? `socket` : this.rtspHost].url
      return url.replace('%s', this.janusServerUrl)
    }
  },
  mounted() {
    this.player = false

    this.$nextTick(function () {
      this.load();
    })
  },
  methods: {
    load(){
      let vm = this
      let janus = null;
      let opaqueId = "streamingtest-"+Janus.randomString(12);

        // Initialize the library (all console debuggers enabled)
        Janus.init({debug: "all", callback: function() {
        // Use a button to start the demo
        $(this).attr('disabled', true).unbind('click');
        // Make sure the browser supports WebRTC
        if(!Janus.isWebrtcSupported()) {
          bootbox.alert("No WebRTC support... ");
          return;
        }
        // Create session
        vm.janus = new Janus(
          {
            server: vm.rtspServerUrl,
            success: function() {
              // Attach to streaming plugin
              vm.janus.attach(
                {
                  plugin: "janus.plugin.gstreamer",
                  opaqueId: opaqueId,
                  success: function(pluginHandle) {
                    vm.streaming = pluginHandle;
                    Janus.log("Plugin attached! (" + vm.streaming.getPlugin() + ", id=" + vm.streaming.getId() + ")");

                    vm.startStream()
                  },
                  error: function(error) {
                    vm.error("-- Error attaching plugin...:", error)
                  },
                  onmessage: function(msg, jsep) {
                    Janus.debug(" ::: Got a message :::");
                    Janus.debug(msg);
                    let result = msg["result"];
                    if(result !== null && result !== undefined) {
                      if(result["status"] !== undefined && result["status"] !== null) {
                        let status = result["status"];
                        if(status === 'stopped')
                          vm.stopStream();
                      }
                    } else if(msg["error"] !== undefined && msg["error"] !== null) {
                      bootbox.alert(msg["error"]);
                      vm.stopStream();
                      return;
                    }
                    if(jsep !== undefined && jsep !== null) {
                      Janus.debug("Handling SDP as well...");
                      Janus.debug(jsep);
                      // Offer from the plugin, let's answer
                      vm.streaming.createAnswer(
                        {
                          jsep: jsep,
                          media: { audioSend: false, videoSend: false },  // We want recvonly audio/video

                          success: function(jsep) {
                            Janus.debug("Got SDP!");
                            Janus.debug(jsep);
                            let body = { "request": "start" };
                            vm.streaming.send({"message": body, "jsep": jsep});
                            vm.startStream()
                          },
                          error: function(error) {
                            vm.error("WebRTC error:", error)
                          }
                        });
                    }
                  },
                  onremotestream: function(stream) {
                    Janus.debug(" ::: Got a remote stream :::");
                    Janus.debug(stream);
                    Janus.attachMediaStream( vm.playerEl, stream);
                  },
                  oncleanup: function() {
                    Janus.log(" ::: Got a cleanup notification :::");
                  }
                });
            },
            error: function(error) {
              vm.error("Error:", error)
            },
          });
        }});
    },
    startStream(){
      if(this.rtspSrc === undefined || this.rtspSrc === null || !this.rtspSrc) {
        bootbox.alert("Enter source MRL");
        return;
      }
      this.isPlaying = true
      let data = { "request": "watch", mrl: this.rtspSrc };
      this.streaming.send({"message": data});
    },
    stopStream() {
      this.streaming.send({"message": { "request": "stop" } });
      this.streaming.hangup();
      this.isPlaying = false
    },
    destroyed(){
      window.location.reload();
    },
    error(message, error) {
      Janus.error(message, error);
      bootbox.alert(`${message}... ${JSON.stringify(error)}`);
    },
    pause(){
      this.player.pause()
    },
    play(){
      this.player.play()
    },
    stop(){
      this.player.stop()
    }
  }
}
</script>
